import React, { useEffect, useState } from "react";
import "./styles.css";

const url = "https://type.fit/api/quotes";
let data;
// const randomNo = () => Math.floor(Math.random() * data.length) + 1;

export default function App() {
  const [quotes, setQuotes] = useState([]);
  const [num, setNum] = useState(0)

  useEffect(() => {
    getQuotes();
  }, []);

  async function getQuotes() {
    try {
      const res = await fetch(url);
      data = await res.json();
      console.log(data)
      setQuotes(data);
    } catch (err) {
      console.log(err);
    }
  }

  function next() {
    setNum(num + 1);
  }

  function previous() {
    setNum(num - 1);
  }

  return (
    <div className="App">
      <h1 className="title">Quotes Generator</h1>
      <p className="quotes">{quotes[num] && quotes[num].text}</p>
      <p className="author">- {quotes[num] && quotes[num].author ? quotes[num].author : "Anonymous"}</p>
      <button className="button" disabled={num === 0 ? true : false} onClick={previous}>
        Previous
			</button>
      <button className="button" onClick={next}>
        Next
			</button>
    </div>
  );
}

